body > div.footer-container > footer > a > img{
    
    animation: moveFooter 10s infinite;
    float: right;
    display: block;

}

body > div.footer-container > footer > div {
    display: block;
    color:black;
    margin-left: 20%;
    margin-top: 25px;
}
body > div.footer-container > footer > div > img{
    margin-left: 10%
}

@keyframes moveFooter {
    0% { clip-path: polygon(0% 100%, 0% 81%, 0% 19%, 0% 0%, 20% 0%, 20% 20%, 0% 20%, 0% 25%, 20% 25%, 20% 100%, 25% 100%, 25% 25%, 100% 25%, 100% 20%, 25% 20%, 25% 0%, 100% 0%, 100% 63%, 100% 100%, 70% 100%, 20% 100%);}
    50%  {clip-path: polygon(0% 100%, 0% 96%, 0% 19%, 0% 0%, 75% 0%, 75% 85%, 0% 85%, 0% 90%, 75% 90%, 75% 100%, 80% 100%, 80% 90%, 100% 90%, 100% 85%, 80% 85%, 80% 0%, 100% 0%, 100% 90%, 100% 100%, 80% 100%, 28% 100%);}
    100% { clip-path: polygon(0% 100%, 0% 81%, 0% 19%, 0% 0%, 20% 0%, 20% 20%, 0% 20%, 0% 25%, 20% 25%, 20% 100%, 25% 100%, 25% 25%, 100% 25%, 100% 20%, 25% 20%, 25% 0%, 100% 0%, 100% 63%, 100% 100%, 70% 100%, 20% 100%);}
  

}
  